.item {
  width: 13em;
  height: 13em;
  margin: 25px 25px;
  font-size: 22px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.todo-title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin: 3px;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.todo-title .todo-close {
  margin: 3px;
  font-size: 30px;
  margin-right: 4px;
}

.todo-title .todo-close:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  cursor: pointer;
}

.todo-description {
  padding: 10px;
  word-wrap: break-word;
  -webkit-flex-grow: 3;
          flex-grow: 3
}

.item:nth-child(3n) {
  -webkit-transform: rotate(-6deg);
          transform: rotate(-6deg);
}

.item:nth-child(3n-1) {
  -webkit-transform: rotate(7deg);
          transform: rotate(7deg);
}

.item:nth-child(3n-2) {
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

.todo-complete {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  margin: 3px;
}

.edit-btn {
  border: 0;
  background: black;
  color: white;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 0.8rem;
  line-height: 1;
  font-size: .7rem
}

.edit-btn:hover {
  cursor: pointer;
}

.check-mark:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  cursor: pointer;
}

.fromNow {
  font-size: 1rem;
}

.completed {
  background-color: #fff !important;
  background-image: linear-gradient(rgba(124,252,0,0.7), rgba(0,100,0,0.7)), url('https://www.iconsdb.com/icons/preview/green/checkmark-xxl.png');
  background-size: cover;
  background-position: center;
}
.modal-container {
    background-color: rgba(0,0,0,0.7);
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
}

.close-modal {
    position: absolute;
    right: 5%;
    top: 3%;
}

.close-modal:hover {
    cursor: pointer;
}

.inner-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 30%;
    background-color: white;
    height: 30vh;
    text-align: center;
    overflow: auto;
}

.title {
    margin: 20px 0;
    font-size: 28px;
}

.inner-modal input,
.inner-modal textarea {
    display: block;
    margin: 20px auto;
    width: 60%;
}

.inner-modal textarea {
     height: 100px;
}

.update-btn {
    border: 0;
    background: #78f89f;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    line-height: 1;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Indie Flower', cursive;
}

html, body {
  height: 100vh;
  margin: 0;
}

#root {
  height: 100%;
}

.container {
  min-height: 100%;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

h1 {
  padding: 10px 0;
}

.todos {
  width: 95%;
  -webkit-flex: 1 1;
          flex: 1 1;
  min-height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 30px;
}

.todos-list {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.todos-adding {
  height: 40px;
  margin-bottom: 20px;
}

.todos-adding input {
  width: 45%;
  height: 100%;
  font-size: 16px;
  padding: 5px;
}

.todos-adding button {
  height: 100%;
  width: auto;
  font-size: 16px;
  padding: 5px;
  margin-left: 10px;
}
